.hero-damage-chart-container{
  overflow: auto; 
  width: 800px;

}

.hero-damage-chart{
  display: flex;
  width: calc(100% - 1px);
}

.hero-damage-chart-index-container{
  display: flex;
  flex-direction: column;
  list-style-type: none;
  padding-top: 30px;
  padding-left: 0px;
  row-gap: 10px;
  margin-top: 30px;
}

.hero-damage-chart-index{
  width: 100px;
  height: 90px; 
  display: flex;
  align-items: center;
  padding: 15px;
  font-weight: 700;
}

.hero-damage-images-dmg-container{
  display: flex;
  flex-direction: row;
  list-style-type: none;
  padding-left: 40px;
  column-gap: 40px;
  cursor: pointer;
}

.hero-damage-images-dmg-headers{
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  font-weight: 700;
  white-space: nowrap;
}

.hero-damage-images-dmg-illustration{
  list-style-type: none;
  padding-left: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 30px;
}

.hero-damage-images-dmg-img-container{
  width: 100px;
  height: 80px;
  padding: 15px;
  position:relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero-skill-img {
    box-shadow: 0px 3px 5px rgb(119, 119, 119);
    border-radius: 50%;
    transition: 200ms all;
  }
  
.hero-skill-img:hover {
    transform: scale(1.7);
  }

.hero-dmg-val{
  position:absolute;
  bottom: 0px;
  right:0px;
}
  